<template>
  <div>
    <div v-if="!isSuccess" class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar
          class="mr-1"
          size="40"
          variant="light-success"
        >
          <img
            :src="require('@/assets/icons/other-type.svg')"
            alt=""
            draggable="false"
            width="30"
          >
        </b-avatar>
        <h4 class="m-0">
          ثبت بدهی متفرقه
        </h4>
      </div>
      <span>
        بدهی متفرقه به صورت موردی برای یک یا چند ساکن یا مالک ثبت می شود. مانند استفاده از آسانسور برای اسباب‌کشی یا خسارت به آسانسور و ...
      </span>
    </div>
    <other-charge-success
      v-if="isSuccess"
      :data="successResponse"
    />
    <b-overlay
      variant="white"
      :show="showLoading"
      spinner-variant="primary"
      blur="0"
      opacity=".75"
      rounded="sm"
    >
      <b-card v-if="!isSuccess">
        <b-form @submit.prevent="submitForm">
          <label
            style="font-size: 14px;
          font-weight: 500;"
            class=""
            for=""
          >مبلغ بدهی</label>
          <b-form-input
            id=""
            v-model="otherFee"
            @input="separate"
            style="text-align: left; font-size:1.2rem; font-weight: 600; direction: ltr;"
            class="mb-2"
            placeholder=""
            size="lg"
          />

          <label
            class=""
            for=""
            style="font-size: 14px;
         font-weight: 500;"
          >عنوان بدهی</label>
          <b-form-input
            id=""
            v-model="otherTitle"
            style="font-size: 1.2rem;font-weight:600;"
            class="mb-2"
            placeholder=""
            size="lg"
          />

          <label
            class="h6"
            for=""
          >انتخاب واحد موردنظر</label>
          <b-form-select
            id=""
            v-model="selectedUnit"
            class="mb-2 font-weight-bolder"
            placeholder=""
            size="lg"
            :options="unitList"
          />
          <div
            v-if="selectedUnit"
            class="mb-2"
          >
            <div>
              <h5>مشمول بدهی</h5>
            </div>
          </div>

          <div
            v-if="selectedUnit"
            class="mb-2"
          >
            <div>
              <b-form-group>
                <b-form-radio
                  v-model="selectedUserType"
                  value="resident"
                  class="mb-1"
                >
                  <span class="h5">ساکن :</span>
                  <span class="h5">{{ currentSelectedData.resident.name }}</span>
                </b-form-radio>

                <b-form-radio
                  v-model="selectedUserType"
                  value="owner"
                >
                  <span class="h5">مالک :</span>
                  <span class="h5">{{ currentSelectedData.owner.name }}</span>
                </b-form-radio>
              </b-form-group>
            </div>

          </div>
          <b-button
            v-if="!pending"
            block
            type="submit"
            variant="primary"
          >
            <span class="btn-save">ثبت</span>
          </b-button>

          <b-button
            v-if="pending"
            block
            type="submit"
            variant="primary"
          >
            <b-spinner
              small
              type="grow"
            />

          </b-button>
        </b-form>
      </b-card>
    </b-overlay>
  </div>
</template>

<script>
import {
  BCard,
  BForm,
  BFormInput,
  BFormSelect,
  BFormSelectOption,
  BFormGroup,
  BFormRadio,
  BFormRadioGroup,
  BFormCheckbox,
  BAvatar,
  BRow,
  BCol,
  BButton,
  BSpinner,
  BOverlay
} from "bootstrap-vue";
import axios from "@/libs/axios";
import DebtRegistrationApis from '@/services/apis/CurrentCharge'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import OtherChargeSuccess from './OtherChargeSuccess.vue';
import { separator } from "@core/helper";

const otherCharge = new DebtRegistrationApis()

export default {
  components: {
    BCard,
    BForm,
    BFormInput,
    BFormSelect,
    BFormSelectOption,
    BFormGroup,
    BFormRadioGroup,
    BFormCheckbox,
    BAvatar,
    BRow,
    BCol,
    BButton,
    OtherChargeSuccess,
    BFormRadio,
    BSpinner,
    BOverlay
  },

  data() {
    return {
      isSuccess: false,
      showLoading: false,
      pending: false,
      successResponse: [],
      currentSelectedData: {
        owner: {
          name: "",
          user_unit_id: "",
          debtor_id: ""

        },
        resident: {
          name: "",
          user_unit_id: "",
          debtor_id: ""
        },
      },
      otherFee: "",
      otherTitle: "",
      unitOwnerAndResidentData: [],
      selectedUnit: "",
      unitList: [],

      selectedUserType: "",
      usersType: [
        { text: "ساکن", value: "resident" },
        { text: "مالک", value: "owner" },
      ],
      token: '',
    };
  },
  watch: {
    selectedUnit(newVal) {
      const data = this.unitOwnerAndResidentData.find(
        (item) => +item.unit_id === +newVal
      );
      if (data) {
          this.currentSelectedData.owner.name = data.owner.name;
          this.currentSelectedData.owner.user_unit_id = data.owner.user_unit_id;
          this.currentSelectedData.owner.debtor_id = data.owner.debtor_id;

          this.currentSelectedData.resident.name = data.resident.name;
          this.currentSelectedData.resident.user_unit_id = data.resident.user_unit_id;
          this.currentSelectedData.resident.debtor_id = data.resident.debtor_id;
      }

    },
  },
  async created() {
    this.showLoading = true
    this.token = localStorage.getItem('token')
    // ;
    try {
      const { data: unitList } = await axios.get('/manager/unit', {
        headers: {
          Authorization: `Bearer ${this.token}`
        }
      });
      this.unitOwnerAndResidentData = unitList.list;
      const unitListArray = [];
      Object.keys(unitList.list).forEach((item) => {
        unitListArray.push({
          text: `${unitList.list[item].unit_name}`,
          value: unitList.list[item].unit_id,
        });
        // eslint-disable-next-line eqeqeq
        if (unitList.list[item].unit_id == this.$route.query.unit_id) {
           this.currentSelectedData.owner.name = unitList.list[item].owner.name;
        this.currentSelectedData.resident.name = unitList.list[item].resident.name;
        }
      })

      this.unitList = unitListArray;
    if (this.$route.query.debtor_id === '2') {
    this.selectedUserType = 'owner';
    } else {
      this.selectedUserType = 'resident'
    }
    this.showLoading = false
    // this.selectedUnit(this.selectedUnit)
    } catch (err) {
      console.log(err);
    }
  },
  mounted() {
    this.otherFee = this.$route.query.debt_fee
    this.otherTitle = this.$route.query.debt_name
    this.selectedUnit = this.$route.query.unit_id
  },
  methods: {
      separate() {
          this.otherFee = separator(this.otherFee)
      },
    // async submitForm() {
    //   // /manager/debt/store-etc
    //   try {
    //     const res = await axios.post("/manager/debt/store-etc", {
    //       debt_fee: this.otherFee,
    //       debt_title: this.otherTitle,
    //       user_unit_id:
    //         this.selectedUserType === "resident"
    //           ? this.currentSelectedData.resident.user_unit_id
    //           : this.currentSelectedData.owner.user_unit_id,
    //       debtor_id:
    //         this.selectedUserType === "resident"
    //           ? this.currentSelectedData.resident.debtor_id
    //           : this.currentSelectedData.owner.debtor_id,
    //     }, {
    //       headers: {
    //         Authorization: `Bearer ${this.token}`
    //       }
    //     });
    //     this.isSuccess = true
    //     this.successResponse = res.data
    //     // console.log(res)
    //   } catch (err) {
    //     console.log(err);
    //   }
    // },
     async submitForm() {
      this.pending = true
      const selectedDebtorId = this.selectedUserType === 'owner' ? 2 : 1;
       // console.log(this.selectedUserType,this.unitOwnerAndResidentData)
       let selectedUnit = this.unitOwnerAndResidentData.find((c) => c.unit_id === this.selectedUnit)
      const data = {
        // unit_debt_id: this.$route.query.id,
        debt_fee: this.otherFee,
        debt_title: this.otherTitle,
        unit_id: this.selectedUnit,
        user_unit_id: this.selectedUserType === 'resident' ? selectedUnit.owner.user_unit_id : selectedUnit.resident.user_unit_id,
        debtor_id: selectedDebtorId,
      }
      await otherCharge.debtStore(data).then(res => {
      this.$toast({
          component: ToastificationContent,
          props: {
            title: res.data.message,
            icon: 'EditIcon',
            variant: 'success',
          }
        })
        this.pending = false
          this.isSuccess = true
          this.successResponse = res.data
        // this.$router.push('/debt-registration/other')
      })
     }
  },
};
</script>

<style lang="scss" scoped>
@import "@core/scss/vue/pages/dashboard-ecommerce.scss";

.btn-save {
  font-size: 20px;
}
</style>
